import React from "react";
import {
  Radar,
  RadarChart,
  PolarGrid,
  Legend,
  PolarAngleAxis,
  PolarRadiusAxis,
} from "recharts";
const RaderGraph = ({resultFam,resultInno,resultMar,resultBure,firstQues,secondQues,thirdQues,fourthQues,fifthQues,sixthQues}) => {
  const items = [
    { culture: "", A: resultFam },
    { culture: "", A: resultInno },
    { culture: "", A: resultMar },
    { culture: "", A: resultBure },
  ];
  // if(firstQues ===100 && secondQues ===100 && thirdQues ===100 && fourthQues ===100 && fifthQues === 100 && sixthQues ===100){
    return(
      <>
        <RadarChart // 
          cx={250} // (0)
          cy={250} // (0)
          outerRadius={150} // 
          width={700} // ()
          height={500} // 
          data={items} // 
        >
          {/*  */}
          <PolarGrid />
          {/* () */}
          <PolarAngleAxis dataKey="culture" />
  
          {/*   */}
          <PolarRadiusAxis
            angle={30} // 0 90
            domain={[0, 50]} // 2
          />
  
          {/*  */}
          <Radar
            name="" // ()
            dataKey="A" // data
            stroke="#8884d8" // 
            fill="#8884d8" // 
            fillOpacity={0.6} // (1MAX)
          />
  
          {/* AB */}
          <Legend />
        </RadarChart>
        
          <div className="flex">
            <div><p></p></div>
            <div><p>{Math.round(resultFam) }pt</p></div>
          </div>
          <div className="flex">
            <div><p></p></div>
            <div><p>{Math.round(resultInno) }pt</p></div>
          </div>
          <div className="flex">
            <div><p></p></div>
            <div><p>{Math.round(resultMar)}pt</p></div>
          </div>
          <div className="flex">
            <div><p></p></div>
            <div><p>{Math.round(resultBure)}pt</p></div>
          </div>
        
       
      </>
    )
  }
  
// }

export default RaderGraph